
.container {
    /*height: calc(100vh - 40px);*/
    height: 100vh;
    background: url("https://wallpapercave.com/wp/6SLzBEY.jpg") no-repeat left top;
    background-size: cover;
    overflow: hidden;
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    margin-top: -40px;
}

.container h1 {
    color: #011718;
    margin-top: -200px;
    font-size: 15em;
    text-align: center;
    text-shadow: -5px 5px 0 rgba(0, 0, 0, 0.7), -10px 10px 0 rgba(0, 0, 0, 0.4), -15px 15px 0 rgba(0, 0, 0, 0.2);
    font-family: monospace;
    font-weight: bold;
}

.container h2 {
    color: black;
    font-size: 5em;
    text-shadow: -5px 5px 0 rgba(0, 0, 0, 0.7);
    text-align: center;
    margin-top: -150px;
    font-family: monospace;
    font-weight: bold;
}

.container h3 {
    color: white;
    margin-left: 30px;
    font-size: 2em;
    text-shadow: -5px 5px 0 rgba(0, 0, 0, 0.7);
    margin-top: -40px;
    font-family: monospace;
    font-weight: bold;
}

.torch {
    margin: -100px 0 0 -100px;
    width: 200px;
    height: 200px;
    box-shadow: 0 0 0 9999em #000000f7;
    opacity: 1;
    border-radius: 50%;
    position: fixed;
    background: rgba(0, 0, 0, 0.3);
}

.torch:after {
    content: '';
    display: block;
    border-radius: 50%;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    box-shadow: inset 0 0 40px 2px #000,
    0 0 20px 4px rgba(13, 13, 10, 0.2);
}